*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto",sans-serif;
}
body{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image:url("./assets/bg1.jpg");
    background-position: center;
    background-size: cover;
}
.bmi-container{
    width: 800px;
    height: 450px;
    padding: 20px;
    background-color:#fff;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0,0.1);
    display: flex;
}
.box{
    width: 350px;
    background-image: url("./assets/bmi_bg.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.data{
    flex: 1;
    padding: 15px;
}
h1{
    font-weight: 600;
    margin-bottom: 20px;
    padding: 10px 0px;
    text-transform: uppercase;
    font-size: 20px;
    color:#13C1D9;
}
.input-container{
    margin-bottom: 15px;
}
label{
    display: block;
    margin-bottom: 5px;
    color: #888;
}
input{
    width: 100%;
    padding: 10px;
    border: 1px solid#ccc;
    margin-bottom: 10px;
    border-radius: 4px;
    font-size: 18px;
}
button{
    background-color:#13C1D9;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}
button:hover{
    background-color:#39d0e4;
}
.result{
    border: 1px solid #13C1D9;
    padding: 10px;
    border-radius: 4px;
    margin: 10px 0;
}
.result p{
    margin: 10px 0;
    color: #555;
}
.result p:first-child{
    color: #13C1D9;
    font-weight: bold;
}
.error{
    color: red;
    font-size: 14px;
    margin-bottom: 10px;
}
.data button:nth-of-type(2){
    background-color: rgb(252, 31, 31);
    margin-left: 10px;
}
.data button:nth-of-type(2):hover{
    background-color: rgb(245, 65, 65);
}